
import { defineComponent, ref } from "vue";
import ApiService, { baseUrl } from "@/core/services/ApiService";
import { hideModal } from "@/core/helpers/dom";
import Swal from "sweetalert2";
interface NewProduct {
  title: "",
  description: "",
  category: "",
  type: "Medical",
  trade_price: number,
  mrp: number,
  code: "",
  point: number,
  imagelist: string[],
  customfunds: object[],
  specifications: object[],
  error: string,
}
export default defineComponent({
  name: "main-dashboard",
  data() {
    return {
      newUserType: "",
      baseUrl: baseUrl,
      error: "",
      member: {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      },
      agent: {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      },
      dealer: {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      },
      depot: {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      },
    };
  },
  mounted() {
    console.log("mounted");
  },
  methods: {
    addUser: function(){
      console.log(this.newUserType);
      var data = this.member;
      if (this.newUserType == "Agent"){
        data = this.agent;
      }
      if (this.newUserType == "Dealer"){
        data = this.dealer;
      }
      if (this.newUserType == "Depot"){
        data = this.depot;
      }
      console.log(data);
      this.error = "";
      if( data.name == ''){
        this.error = "Name is required";
        return;
      }
      if( data.username == ''){
        this.error = "Username is required";
        return;
      }
      if (data.usernameAvailable != "yes"){
        this.error = "Username is not available";
        return;
      }
      if( data.mobile == ''){
        this.error = "Mobile is required";
        return;
      }
      if( data.email == ''){
        this.error = "Email is required";
        return;
      }
      if( data.address == ''){
        this.error = "Address is required";
        return;
      }
      if( data.sponsorStatus == '' || data.sponsorStatus == 'error' || data.sponsorStatus == 'checking'){
        this.error = "Valid Sponsor is required";
        return;
      }
      if( data.passwd == ''){
        this.error = "Password is required";
        return;
      }
      if( data.passwd2 == ''){
        this.error = "Confirm Password is required";
        return;
      }
      if( data.passwd != data.passwd2){
        this.error = "Password and Confirm Password must be same";
        return;
      }
      ApiService.post("members", {
        data : {
          info: data,
          type: this.newUserType,
        }
      })
        .then((res) => {
          console.log(res);
          Swal.fire({
              title: "Success",
              text: "Member Added Successfully",
              icon: "success",
              confirmButtonText: "Ok",
            }).then((result) => {
              if (result.isConfirmed) {
                this.closeForm();
              }
            });
        })
        .catch((err) => {
          console.log(err);
          Swal.fire({
            title: "Error",
            text: "Something went wrong",
            icon: "error",
            confirmButtonText: "Ok",
          });
        });
    },
    checkUsername: function(){
      var data = this.member;
      if (this.newUserType == "Agent"){
        data = this.agent;
      }
      if (this.newUserType == "Dealer"){
        data = this.dealer;
      }
      if (this.newUserType == "Depot"){
        data = this.depot;
      }
      data.usernameAvailable = "checking";
      if (data.username == ""){
        data.usernameAvailable = "";
        return;
      }
      ApiService.get("/check-username?username="+data.username)
        .then((res) => {
          data.usernameAvailable = "no";
        })
        .catch((err) => {
          console.log(err);
          data.usernameAvailable = "yes";
        });
    },
    sponsorCheck: function(){
      var data = this.member;
      if (this.newUserType == "Agent"){
        data = this.agent;
      }
      if (this.newUserType == "Dealer"){
        data = this.dealer;
      }
      if (this.newUserType == "Depot"){
        data = this.depot;
      }
      console.log(data.sponsor);
      if (data.sponsor.length > 1){
        data.sponsorStatus = "checking";
        ApiService.setHeader();
        ApiService.get("/check-user?username="+ data.sponsor).then((res: any) => {
          console.log(res);
          data.sponsorStatus = res.data;
        })
        .catch((err: any) => {
          console.log(err);
          data.sponsorStatus = "error";
        });
      }
    },
    closeForm: function () {
      this.newUserType = "";
      this.error = "";
      this.member = {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      };
      this.agent = {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      };
      this.dealer = {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      };
      this.depot = {
        name: "",
        username: "",
        usernameAvailable:  "",
        gender: "MALE",
        mobile: "",
        email: "",
        address: "",
        im: "",
        passwd: "",
        passwd2: "",
        sponsor: "",
        sponsorStatus: "",
      }
    },
  },
});
